@tailwind base;
@tailwind components;
@tailwind utilities;
html {
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-text:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

.grecaptcha-badge {
  visibility: hidden;
}

html.lenis {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-scrolling iframe {
  pointer-events: none;
}

.text-highlighted-lime {
  color: #B6ED3E;
}

.text-highlighted-dark {
  color: #032F35;
}

.title-section {
  @apply font-montbold text-3xl lg:text-6xl uppercase mb-6 whitespace-normal break-words;
}

.description-section {
  @apply font-montlight text-lg leading-8 mb-12;
}

.text-body {
  @apply font-sans font-normal text-base leading-8;
}

.color-accent {
  @apply text-lime-green;
}

.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap {
  @apply block mb-4;
}
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap .wpcf7-not-valid-tip {
  @apply block mt-2 px-4 uppercase font-sans font-semibold text-xs;
}
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap .wpcf7-form-control.wpcf7-text,
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap .wpcf7-form-control.wpcf7-textarea {
  @apply w-full border border-green/20 py-3 px-6 rounded-full font-sans text-base text-dark-green;
}
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap .wpcf7-form-control.wpcf7-text::placeholder,
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap .wpcf7-form-control.wpcf7-textarea::placeholder {
  @apply font-sans text-gray-300 text-base;
}
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control-wrap .wpcf7-form-control.wpcf7-textarea {
  @apply w-full rounded-3xl;
}
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-form-control.has-spinner.wpcf7-submit {
  @apply bg-green font-sans font-semibold text-base text-light-green uppercase py-2 px-11 rounded-full transition-all duration-500 ease-in-out hover:text-light-green hover:bg-dark-green hover:cursor-pointer;
}
.page-template-page-contact .wpcf7 .wpcf7-form .wpcf7-response-output {
  @apply font-sans font-medium text-sm rounded-full py-2 px-6;
}
.page-template-page-contact .wpcf7 .wpcf7-form.invalid .wpcf7-response-output {
  @apply font-sans font-medium text-sm rounded-full py-2 px-6 bg-yellow-400 border-yellow-400;
}
.page-template-page-contact .wpcf7 .wpcf7-form.sent .wpcf7-response-output {
  @apply font-sans font-medium text-sm text-white rounded-full py-2 px-6 bg-[#46b450] border-[#46b450];
}